.container{
  width: 100%;
}

.row-layout {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(10px, 1fr));
  width: 100%;
}

.widget-container{
  position: relative;
  width: 100%;
}

.row-container{
  position: relative;
  width: 100%;
  padding-top: 3px;
  padding-bottom: 3px;
}

.drop-handler{
  position: absolute;
  z-index: 10;
  background-color: rgba(33, 150, 243, 0.05); /* 항상 약간 보임 */
  border: 1px dashed rgba(33, 150, 243, 0.2); /* 점선 테두리 */
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  
  &:hover {
    background-color: rgba(33, 150, 243, 0.1);
    border-color: rgba(33, 150, 243, 0.4);
  }
  
  &::before {
    content: '';
    position: absolute;
    opacity: 0;
    transition: opacity 0.2s ease;
  }
  
  &::after {
    content: '⋮⋮'; /* 점 3개로 드래그 영역 표시 */
    position: absolute;
    color: rgba(33, 150, 243, 0.3);
    font-size: 16px;
    font-weight: bold;
    opacity: 0.5;
    transition: opacity 0.2s ease;
  }
}

.top, .bottom{
  width: 100%;
  height: 30px; /* 20px에서 30px로 증가 */
  border-left: none;
  border-right: none;
  
  &::before {
    width: 100%;
    height: 2px;
    background: rgba(33, 150, 243, 0.3);
    border-radius: 2px;
  }
  
  &::after {
    content: '⋯⋯⋯'; /* 가로 점선 */
    font-size: 20px;
    letter-spacing: 4px;
  }
}

.left, .right{
  width: 30px; /* 20px에서 30px로 증가 */
  height: 100%;
  border-top: none;
  border-bottom: none;
  
  &::before {
    width: 2px;
    height: 100%;
    background: rgba(33, 150, 243, 0.3);
    border-radius: 2px;
  }
  
  &::after {
    content: '⋮'; /* 세로 점선 */
    font-size: 20px;
    line-height: 8px;
  }
}

.drop-handler.top{
  top: -15px; /* 중앙 정렬 */
}

.drop-handler.bottom{
  bottom: -15px; /* 중앙 정렬 */
}

.drop-handler.left{
  left: -15px; /* 중앙 정렬 */
}

.drop-handler.right{
  right: -15px; /* 중앙 정렬 */
}

.drop-handler.hovered{
  background: linear-gradient(to bottom, rgba(33, 150, 243, 0.3), rgba(33, 150, 243, 0.1)) !important;
  border: 3px solid #2196F3 !important;
  border-radius: 6px;
  box-shadow: 0 2px 12px rgba(33, 150, 243, 0.6);
  animation: pulse 1s ease-in-out infinite;
  
  &::before {
    opacity: 1;
    background: #2196F3 !important;
    height: 4px !important;
    box-shadow: 0 0 8px rgba(33, 150, 243, 0.8);
  }
  
  &::after {
    content: '↓ 여기에 놓기' !important;
    position: absolute;
    color: white !important;
    font-size: 13px !important;
    font-weight: bold;
    background: #2196F3 !important;
    padding: 4px 12px !important;
    border-radius: 4px;
    box-shadow: 0 2px 8px rgba(0,0,0,0.3);
    white-space: nowrap;
    opacity: 1 !important;
    letter-spacing: normal !important;
  }
}

.top.hovered::after {
  content: '↑ 위에 놓기' !important;
}

.bottom.hovered::after {
  content: '↓ 아래에 놓기' !important;
}

.left.hovered::after {
  content: '← 왼쪽에 놓기' !important;
}

.right.hovered::after {
  content: '→ 오른쪽에 놓기' !important;
}

.top.hovered::before,
.bottom.hovered::before {
  width: 100% !important;
  height: 4px !important;
}

.left.hovered::before,
.right.hovered::before {
  width: 4px !important;
  height: 100% !important;
}

.handler-row.hovered{
  background: linear-gradient(to bottom, rgba(76, 175, 80, 0.3), rgba(76, 175, 80, 0.1));
  border: 2px solid #4CAF50;
  border-radius: 6px;
  box-shadow: 0 2px 8px rgba(76, 175, 80, 0.4);
  animation: pulse 1s ease-in-out infinite;
}

@keyframes pulse {
  0%, 100% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.02);
    opacity: 0.9;
  }
}
